<template>
    <div class="addPeoplePosition">
        <div class="title">{{title}}</div>
        <el-form :model="form" :rules="rules" ref="form" label-width="130px">
            <el-form-item label="" prop="name">
                <el-input v-model="form.name" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item label="" prop="code">
                <el-input v-model="form.code" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item label="" prop="reqUri">
                <el-input v-model="form.reqUri" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item label="" prop="reqMethod">
                <el-input v-model="form.reqMethod" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item label="" prop="reqParam">
                <el-input v-model="form.reqParam" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item label="" prop="reqDefaultParamValue">
                <el-input type="textarea" v-model="form.reqDefaultParamValue" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item label="" prop="reqBefore">
                <el-input type="textarea" v-model="form.reqBefore" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item label="" prop="resExplain">
                <el-input type="textarea" v-model="form.resExplain" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item label="" prop="reqKeepalive">
                <el-radio v-model="form.reqKeepalive" label="true" :disabled="disableValue"></el-radio>
                <el-radio v-model="form.reqKeepalive" label="false" :disabled="disableValue"></el-radio>
            </el-form-item>
            <el-form-item label="" prop="reqKeepaliveTimeout">
                <el-input v-model="form.reqKeepaliveTimeout" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item label="" prop="reqKeepalivePool">
                <el-input v-model="form.reqKeepalivePool" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item label="" prop="reqHeaders">
                <el-input v-model="form.reqHeaders" :disabled="disableValue"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="save" v-if="this.mode!= 'read'"></el-button>
                <el-button @click="goBack"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import 'async-validate'
    export default {
        name: "thirdInterfaceAdd",
        props: {
            editId: '',
            mode: ''
        },
        data () {
            return {
                form: {
                    name: '',
                    code: '',
                    reqUri: '',
                    reqMethod: '',
                    reqParam: '',
                    reqDefaultParamValue: '',
                    reqBefore: '',
                    resExplain: '',
                    reqKeepalive: '',
                    reqKeepaliveTimeout: '',
                    reqKeepalivePool: '',
                    reqHeaders: ''
                },
                crowdIds: [],
                title: '',
                disableValue: false,
                rules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                        { message: '100', trigger: 'blur'}
                    ],
                    code: [
                        {required:true, message:'', trigger: 'blur'},
                        {pattern: /^[a-zA-Z][a-zA-Z_0-9_]{0,99}$/, message: '100', trigger: 'blur'}
                    ],
                    reqUri: [
                        {required:true, message:'', trigger: 'blur'},
                        {min: 1, max: 200, message: '200', trigger: 'blur'}
                    ],
                    reqMethod: [
                        {required:true, message:'', trigger: 'blur'},
                        {min: 1, max: 20, message: '20', trigger: 'blur'}
                    ],
                    reqParam: [
                        {required:true,message:'',trigger: 'blur'},
                        {min: 1, max: 100, message: '100', trigger: 'blur'}
                    ],
                    reqDefaultParamValue: [
                        {required:false,message:'',trigger: 'blur'},
                        {min: 0, max: 1000, message: '1000', trigger: 'blur'}
                    ],
                    reqBefore: [
                        {required:false,message:'',trigger: 'blur'}
                        // {min: 0, max: 2000, message: '2000', trigger: 'blur'}
                    ],
                    resExplain: [
                        {required:true,message:'',trigger: 'blur'}
                        // {min: 1, max: 2000, message: '2000', trigger: 'blur'}
                    ],
                    reqKeepaliveTimeout: [
                        {pattern:/^\d+$/,message:'0',trigger: 'blur'}
                    ],
                    reqKeepalivePool: [
                        {pattern:/^\d+$/,message:'0',trigger: 'blur'}
                    ],
                    reqHeaders: [
                        {required:false,type: "string",message:'',trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            getDetail () {
                this.$service.getThirdInterfaceDetail({id: this.editId}).then(
                    (data) => {
                        this.form = data
                        // if(this.mode === 'edit') {this.form.id = this.editId}
                    }
                )
            },
            // [{label, value}]
            // _arrayingOption(arr, label, value) {
            //     return arr.reduce((result, item) => {
            //         return result.concat({ label: item[label], value: item[value] })
            //     }, [])
            // },
            goBack () {
                this.$emit('open-list-page')
            },
            save () {
                this.$refs.form.validate(valid => {
                    if(valid) {
                        if (this.editId === undefined) {
                            this.$service.getthirdInterfaceAdd(this.form, '').then( () => {
                                this.$emit('open-list-page')
                            })
                        }else {
                            const submitData = {
                                id: this.editId,
                                code: this.form.code,
                                name: this.form.name,
                                reqUri: this.form.reqUri,
                                reqMethod: this.form.reqMethod,
                                reqParam: this.form.reqParam,
                                reqDefaultParamValue: this.form.reqDefaultParamValue,
                                reqBefore: this.form.reqBefore,
                                resExplain: this.form.resExplain,
                                reqKeepalive: this.form.reqKeepalive,
                                reqKeepaliveTimeout: this.form.reqKeepaliveTimeout,
                                reqKeepalivePool: this.form.reqKeepalivePool,
                                reqHeaders: this.form.reqHeaders
                            }
                            this.$service.getthirdInterfaceEdit(submitData, '').then( () => {
                                this.$emit('open-list-page')
                            })
                        }
                    }
                })
            },
        },
        created () {
            if(this.editId === undefined){
                this.title = ''
                this.form.protocol = 'http'
            }else {
                if(this.mode === 'edit') {
                    this.title = ''
                    this.getDetail()

                }else {
                    this.title = ''
                    this.getDetail()
                    this.disableValue = true
                }
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .addPeoplePosition
        width 80%
        margin-left 20px
    .title
        margin 20px
        font-size 30px
    .example
        font-size 12px
        color #ccc
    .example div
        overflow auto
</style>
