<template>
    <div>
        <div>
            <el-button></el-button>
        </div>
        <el-table :data="tableData" border>
            <el-table-column label="" type="index"></el-table-column>
            <el-table-column label="id" prop="crowdId"></el-table-column>
            <el-table-column label="" prop="crowdName"></el-table-column>
            <el-table-column label="id" prop="pcrowdId"></el-table-column>
            <el-table-column label="" prop="pcrowdName"></el-table-column>
            <el-table-column label="" prop="apiStatus">
                <template slot-scope="scope">
                    <span v-if="scope.row.apiStatus === 1"></span>
                    <span v-if="scope.row.apiStatus === 0">
                      <el-tooltip placement="right-start">
                        <div v-if="scope.row.putway === 0" slot="content"></div>
                        <div v-else slot="content">{{unActiveTips(scope.row.crowdValidStatus)}}</div>
                        <span class="uneffective"><span>?</span></span>
                      </el-tooltip>
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    {{crowdValidEnum[scope.row.crowdValidStatus] || ''}}
                </template>
            </el-table-column>
            <el-table-column label="" width="350">
                <template slot-scope="scope">
                    <el-button></el-button>
                    <el-button></el-button>
                    <el-button></el-button>
                    <el-button></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div align="right">
            <pagination
                    v-bind:currentpage="currentPage"
                    v-bind:pagesize="pageSize"
                    v-bind:totalcount="totalCount"
                    @handle-size-change="handleSizeChange"
                    @handle-current-change="handleCurrentChange"
            ></pagination>
        </div>
    </div>
</template>

<script>
    export default {
        name: "List",
        data () {
            return {
                tableData: [],
                crowdValidEnum: {1: "", 2: "", 3: ""},
                pageSize: 10,
                currentPage: 1,
                totalCount: 1,
                filter: {},
                searchCont: undefined
            }
        },
        watch : {
            '$route.params.redirectListId': function (val,oldVal) {
                if(val!= oldVal && val) {
                    this.fetchData()
                }
            }
        },
        methods: {
            fetchData () {
                this.parseFilter()
                this.$service.getRedirectCrowdList(this.filter).then(data => {
                    this.tableData = data.list
                    this.totalCount = data.total
                })
            },
            parseFilter () {
                const crowdId = this.$route.params.redirectListId
                this.filter = {
                    pageSize: this.pageSize,
                    pageNum: this.currentPage,
                    searchCont: this.searchCont,
                    crowdId
                }
            },
            unActiveTips (activeType) {
                if (activeType == 1 || activeType == 3) {
                    return '' + this.crowdValidEnum[activeType]
                } else {
                    return '""'
                }
            },
            handleSizeChange (val) {
                this.pageSize = val
                this.fetchData()
            },
            // , 12,val=2
            handleCurrentChange (val) {
                this.currentPage = val
                this.fetchData()
            },
        },
        created () {
            this.fetchData()
        }
    }
</script>

<style lang="stylus" scoped>
.uneffective
    position relative
    cursor pointer
    span
        position absolute
        display inline-block
        width 12px
        height 12px
        border 1px solid
        border-radius 10px
        text-align center
        line-height 12px
</style>
