<template>
    <div>
        <el-dialog
            title=""
            :visible.sync="showCreateDialog"
        >
            <el-form :model="form" ref="form" :rules="rules" label-width="100px">
                <el-form-item label="" prop="pid">
                    <el-input style="display:none;" v-model="form.pid" placeholder=""></el-input>
                    <el-input size="small" readonly v-model="parentGroupName" placeholder=""></el-input>
                    <el-button size="small" type="primary" icon="search" @click="showPidList"></el-button>
                    <!--<el-select v-model="form.pid">-->
                        <!--<el-option v-for="item in items" :label="item.groupName" :value="item.groupId" :key="item.groupId"></el-option>-->
                    <!--</el-select>-->
                </el-form-item>
                <el-form-item label="" prop="groupName">
                    <el-input v-model="form.groupName"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleSubmit('form')"></el-button>
                    <!--<el-button @click="resetForm('ruleForm')"></el-button>-->
                </el-form-item>
            </el-form>
        </el-dialog>
        <!---->
        <el-dialog
                title=""
                :visible.sync="showSelectDialog"
                width="30%"
                class="organSelect"
        >
            <el-button type="text" @click="handleTopMenu"></el-button>
            <el-tree
                    :data="parentTree"
                    :props="parentProps"
                    accordion
                    @node-click="handleSelectNodeClick">
            </el-tree>
            <span slot="footer" class="dialog-footer">
            <el-button @click="parentSelectCancel"> </el-button>
            <el-button type="primary" @click="parentSelectOk"> </el-button>
        </span>
        </el-dialog>
    </div>
</template>
<script>
// import _ from '@/gateschema'
export default {
    data() {
        return {
            showCreateDialog: false,
            showSelectDialog: false,
            parentTree: [],
            parentProps:{
                children:"children",
                label:"groupName"
            },
            parentGroupName: '',
            currentSelectDada: '',
            tagGroup: {},
            items: [],
            form: {
                pid: '',
                groupName: ''
            },
            rules: {
                pid: [
                    { required: true, message: '', trigger: 'change' },
                ],
                groupName: [
                    { required: true, message: '', trigger: 'blur' }
                ],
            },
            selectTop: false
            // schema: _
            //     .map({
            //         groupName: _.r.str.other('form', {label: ''})
            //     })
            //     .other('form', {
            //         footer: {
            //             showSubmit: true,
            //             submitText: ''
            //         }
            //     })
        }
    },
    methods: {
        handleSubmit(form) {
            this.$refs[form].validate((valid) => {
                    if (valid) {
                        this.$service.createTagGroup(this.form, '').then(() => {
                            this.showCreateDialog = false
                            this.$emit('create-end')
                        })
                    } else {
                        return false
                    }
            // if (errors.length === 0) {
            //     this.$service.createTagGroup(this.tagGroup, '').then(() => {
            //         this.showCreateDialog = false
            //         this.$emit('create-end')
            //     })
            // }
            })
        },
        getPidList () {
            this.$service.getParentIdList().then((data) => {
                this.parentTree = data
            })
        },
        showPidList () {
            this.showSelectDialog = true
        },
        handleSelectNodeClick (node) {
            this.selectTop = false
            this.currentSelectDada = node
        },
        handleTopMenu(){
            this.selectTop = true
        },
        parentSelectCancel () {
            this.showSelectDialog = false
        },
        parentSelectOk () {
            if(this.selectTop){
                this.form.pid = 0
                this.parentGroupName = ''
            }else{
                this.form.pid = this.currentSelectDada.groupId
                this.parentGroupName = this.currentSelectDada.groupName
            }
            this.showSelectDialog = false
        },
        resetForm () {
            this.$nextTick(() => {
                this.$refs['form'].resetFields()
            })
            this.parentGroupName = null
        }
    },
    watch: {
        'showSelectDialog': function (val) {
            if (val) { this.getPidList() }
        },
        'showCreateDialog': function (val) {
            if (val) { this.resetForm() }
        }
    },
    created() {
        // this.getPidList()
        // this.form.pid = null
        // this.form.groupName = null
        // this.parentGroupName = null
    }
}
</script>
<style scoped lang="stylus">
    .organSelect >>> .is-current
        color:#3980e2
</style>
