<template>
  <div>
    <!-- authority -->
    <el-button-group>
      <el-button type="primary" size="small" @click="handleAdd" v-permission="'sysSetup:menu:add'">
        <!--TODO: permission -->
        <a class="fa fa-plus" style="color: white;"></a>
      </el-button>
      <!-- <el-button type="primary" size="small" @click="handleRefresh">
                <a class="fa fa-refresh" style="color: white;"></a> 
      </el-button>-->
    </el-button-group>
    <br>
    <br>
    <!-- talbe -->
    <el-table :data="tableData" ref="myTable" style="width: 100%;" stripe border>
      <!--<el-table-column prop="id" label="ID" width="100"></el-table-column>-->
      <el-table-column prop="name" label="" width="250">
        <!--<template slot-scope="scope">
                     <el-button type="text" @click="changeExpandAndOff(scope.$index, scope.row)">
                         <i v-if="!expand" class="el-icon-caret-right"></i>
                         <i v-if="expand" class="el-icon-caret-bottom"></i>
                     </el-button>
                     {{ scope.row.name }}
        </template>-->
        <template slot-scope="scope">
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 1" style="margin-left: 0px"></a>
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 2" style="margin-left: 10px"></a>
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 3" style="margin-left: 20px"></a>
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 4" style="margin-left: 30px"></a>
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 5" style="margin-left: 40px"></a>
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 6" style="margin-left: 50px"></a>
          {{ scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column prop="href" label="" width="300"></el-table-column>
      <el-table-column prop="sort" label="">
        <!--<template slot-scope="scope">-->
          <!--<el-input :value="scope.row.sort" @blur="updateSort(scope.$index, scope.row)"></el-input>-->
        <!--</template>-->
      </el-table-column>
      <el-table-column prop="permission" label="" width="300"></el-table-column>
      <el-table-column prop="isShow" label="">
        <template slot-scope="scope">
          <el-tag type="success" close-transition="false" v-if="scope.row.isShow === 1">
            <a class="fa fa-unlock"></a> 
          </el-tag>
          <el-tag type="danger" close-transition="false" v-if="scope.row.isShow === 0">
            <a class="fa fa-lock"></a> 
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right" width="280">
        <template slot-scope="scope">
          <el-button-group>
            <!--<el-button size="small" type="success" @click="handleDetail(scope.$index, scope.row)">
                            <a class="fa fa-search" style="color: white;"></a> 
            </el-button>-->
            <el-button
              size="small"
              type="success"
              @click="handleChangetStatus(scope.$index, scope.row)"
              v-permission="'sysSetup:menu:changeStatus'"
            >
              <a class="fa fa-edit" style="color: white;"></a> 
            </el-button>
            <el-button
              size="small"
              type="primary"
              @click="handleEdit(scope.$index, scope.row)"
              v-permission="'sysSetup:menu:edit'"
            >
              <a class="fa fa-edit" style="color: white;"></a> 
            </el-button>
            <el-button
              size="small"
              type="info"
              @click="handleDelete(scope.$index, scope.row)"
              v-permission="'sysSetup:menu:del'"
            >
              <a class="fa fa-trash" style="color: white;"></a> 
            </el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <br>
    <!--<el-button @click="saveSort" type="primary"><i class="fa fa-save"></i></el-button>-->
    <!---->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="addFormVisible"
      v-if="addFormVisible"
      v-model="addFormVisible"
      :close-on-click-modal="false"
    >
      <el-form :model="menuForm" :rules="menuFormRules" ref="menuForm" label-width="150px">
        <el-form-item label="" prop="parentId">
          <el-input style="display:none;" v-model="menuForm.parentId"></el-input>
          <el-input size="small" readonly v-model="menuForm.parentName" placeholder=""></el-input>
          <el-button size="small" type="primary" icon="search" @click="selectMenu"></el-button>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="menuForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="href">
          <el-input size="small" v-model="menuForm.href" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="permission">
          <el-input size="small" v-model="menuForm.permission" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="icon">
          <el-input size="small" readonly v-model="menuForm.icon" placeholder=",:fa-book"></el-input>
          <el-button size="small" type="primary" icon="search" @click="selectIcon"></el-button>
        </el-form-item>
        <el-form-item label="" prop="sort">
          <el-input size="small" v-model.number="menuForm.sort" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="menuForm.isShow">
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelAdd"></el-button>
        <el-button type="primary" @click="addSubmit"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="organVisible" width="30%" class="organSelect">
      <el-tree :data="organs" :props="organProps" accordion @node-click="handleNodeClick"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="companySelectCancel"> </el-button>
        <el-button type="primary" @click="companySelectOk"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    const checkSort = (rule, value, callback) => {
        if(!value) {
            return callback(new Error(''))
        }
        if(!Number.isInteger(value)){
            callback(new Error(''))
        } else {
            callback()
        }
    }
    return {
      // 
      tableData: [],
      dialogTitle: "",
      organs: [],
      organProps: {
        chilidren: "children",
        label: "name"
      },
      // 
      addFormVisible: false, // 
      organVisible: false,
      // 
      menuForm: {
        id: "",
        parentId: 0,
        parentName: "",
        name: "",
        href: "",
        permission: "",
        icon: "",
        sort: "",
        isShow: 0
      },
      menuFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        permission: [
          { required: true, message: "", trigger: "blur" }
        ],
        icon: [{ required: true, message: "", trigger: "blur" }],
        sort: [
            {required: true, validator: checkSort, trigger: 'blur'}
        ]
      },

      // 
      editFormVisible: false // 
    };
  },
  props: ["iconName"],
  created() {
    this.loadData();
    this.menuForm.icon = this.iconName;
  },
  watch: {
    iconName (val) {
      this.menuForm.icon = val;
    }
  },
  methods: {
    // 
    loadData () {
      this.$service.get_menus_json().then(data => {
        this.tableData = data.menuTreeTable;
      });
    },
    selectIcon() {
      this.$emit("show", false);
      document.querySelector(".v-modal").style.display = "none";
    },
    // 
    handleChangetStatus (index, row) {
      var id = row.id;
      var isShow = row.isShow == 1 ? 0 : 1;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$service
            .changeMenuStatus({ id: id, isShow: isShow },"")
            .then(() => {
              this.loadData();
            });
        })
        .catch(() => {

        });
    },

    // 
    // handleDetail (index, row) {
    // },
    selectMenu() {
      this.$service.get_menu_tree_json().then(data => {
        this.organs = data;
        this.organVisible = true;
      });
    },
    handleNodeClick(data) {
      this.$appState.$set("company_data", JSON.stringify(data));
    },
    companySelectCancel() {
      this.$appState.$set("company_data", "");
      this.organVisible = false;
    },
    companySelectOk() {
      this.organVisible = false;
      const data = JSON.parse(this.$appState.$get("company_data"));
      this.menuForm.parentId = data.id;
      this.menuForm.parentName = data.name;
    },

    // 
    handleAdd () {
      this.dialogTitle = "";
      this.addFormVisible = true;
      this.menuForm.id = "";
      this.menuForm.parentId = "";
      this.menuForm.parentName = "";
      this.menuForm.name = "";
      this.menuForm.href = "";
      this.menuForm.permission = "";
      this.menuForm.icon = "";
      this.menuForm.sort = "";
      this.menuForm.isShow = 0;
    },

    // 
    addSubmit () {
      const formData = JSON.parse(JSON.stringify(this.menuForm))
      if(formData.parentId === '') {formData.parentId = 0}
      this.$refs.menuForm.validate(valid => {
        if (valid) {
          if (formData.id != "") {
            this.$service.MenuUpdate(formData,"").then(() => {
              this.loadData();
              this.addFormVisible = false;
            });
          } else {
            this.$service.MenuAdd(formData,"").then(() => {
              this.loadData();
              this.addFormVisible = false;
            });
          }
        } else {
          return false;
        }
      });
    },

    // 
    handleEdit (index, row) {
      this.dialogTitle = "";
      this.addFormVisible = true;
      this.menuForm.id = row.id;
      this.menuForm.parentId = row.parentId;
      if (row.parentName == null) {
        this.menuForm.parentName = "";
      } else {
        this.menuForm.parentName = row.parentName;
      }
      this.menuForm.name = row.name;
      this.menuForm.href = row.href;
      this.menuForm.permission = row.permission;
      this.menuForm.icon = row.icon;
      this.menuForm.sort = row.sort;
      this.menuForm.isShow = row.isShow;
    },
    // 
    cancelAdd () {
      this.addFormVisible = false;
    },
    // 
    handleDelete (index, row) {
      var id = row.id;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "error"
      })
        .then(() => {
          this.$service.delMenu({ id: id },"").then(() => {
            this.loadData();
          });
        })
        .catch(() => {

        });
    }
  }
};
</script>
<style lang="stylus" scoped>
.organSelect >>> .is-current .el-tree-node__label
  color: #3c88d0
</style>

