<template>
  <div>
    <!-- authority -->
    <el-button-group>
      <el-button
        type="primary"
        size="small"
        @click="handleAdd"
        v-permission="'sysAdministrative:office:add'"
      >
        <a class="fa fa-plus" style="color: white;"></a>
      </el-button>
      <!-- <el-button type="primary" size="small" @click="handleRefresh">
                <a class="fa fa-refresh" style="color: white;"></a> 
      </el-button>-->
    </el-button-group>
    <br>
    <br>
    <!-- talbe -->
    <el-table :data="tableData" ref="myTable" style="width: 100%;" stripe border>
      <!--<el-table-column prop="id" label="ID" width="100"></el-table-column>-->
      <el-table-column prop="name" label="" width="150">
        <template slot-scope="scope">
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 1" style="margin-left: 0px"></a>
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 2" style="margin-left: 10px"></a>
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 3" style="margin-left: 20px"></a>
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 4" style="margin-left: 30px"></a>
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 5" style="margin-left: 40px"></a>
          <a :class="scope.row.fullIcon" v-if="scope.row.depth === 6" style="margin-left: 50px"></a>
          {{ scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column prop="master" label="" width="80"></el-table-column>
      <el-table-column prop="email" label="" width="150"></el-table-column>
      <el-table-column prop="sort" label="" width="100">
        <template slot-scope="scope">
          <el-input :value="scope.row.sort"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="useable" label="" width="100">
        <template slot-scope="scope">
          <el-tag type="success" close-transition="false" v-if="scope.row.useable === 1">
            <a class="fa fa-unlock"></a> 
          </el-tag>
          <el-tag type="danger" close-transition="false" v-if="scope.row.useable === 0">
            <a class="fa fa-lock"></a> 
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right">
        <template slot-scope="scope">
          <el-button-group>
            <!--<el-button size="small" type="success" @click="handleDetail(scope.$index, scope.row)">
                            <a class="fa fa-search" style="color: white;"></a> 
            </el-button>-->
            <el-button
              size="small"
              type="success"
              @click="handleChangetStatus(scope.$index, scope.row)"
              v-permission="'sysAdministrative:office:changeStatus'"
            >
              <a class="fa fa-edit" style="color: white;"></a> 
            </el-button>
            <el-button
              size="small"
              type="primary"
              @click="handleEdit(scope.$index, scope.row)"
              v-permission="'sysAdministrative:office:edit'"
            >
              <a class="fa fa-edit" style="color: white;"></a> 
            </el-button>
            <el-button
              size="small"
              type="info"
              @click="handleDelete(scope.$index, scope.row)"
              v-permission="'sysAdministrative:office:del'"
            >
              <a class="fa fa-trash" style="color: white;"></a> 
            </el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <br>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addFormVisible"
      v-if="addFormVisible"
      v-model="addFormVisible"
      :close-on-click-modal="false"
    >
      <el-form :model="officeForm" :rules="officeFormRules" ref="officeForm" label-width="150px">
        <el-form-item label="" prop="parentId">
          <el-input style="display:none;" v-model="officeForm.parentId"></el-input>
          <el-input size="small" readonly v-model="officeForm.parentName" placeholder=""></el-input>
          <el-button size="small" type="primary" icon="search" @click="selectOffice"></el-button>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="officeForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sort">
          <el-input size="small" v-model="officeForm.sort" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="master">
          <el-input size="small" v-model="officeForm.master" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input size="small" v-model="officeForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="officeForm.useable">
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelAdd"></el-button>
        <el-button type="primary" @click="addSubmit"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog
      title=""
      :visible.sync="editFormVisible"
      v-if="editFormVisible"
      v-model="editFormVisible"
      :close-on-click-modal="false"
    >
      <el-form :model="officeForm" :rules="officeFormRules" ref="officeForm" label-width="150px">
        <el-form-item label="" prop="parentId">
          <el-input style="display:none;" v-model="officeForm.parentId"></el-input>
          <el-input size="small" readonly v-model="officeForm.parentName" placeholder=""></el-input>
          <el-button size="small" type="primary" icon="search" @click="selectOffice"></el-button>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="officeForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sort">
          <el-input size="small" v-model="officeForm.sort" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="master">
          <el-input size="small" v-model="officeForm.master" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input size="small" v-model="officeForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="officeForm.useable">
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEdit"></el-button>
        <el-button type="primary" @click="editSubmit"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="organVisible" width="30%" class="organSelect">
      <el-tree :data="organs" :props="organProps" accordion @node-click="handleNodeClick"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="companySelectCancel"> </el-button>
        <el-button type="primary" @click="companySelectOk"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'organManageAA',
  data() {
    return {
      // 
      tableData: [],
      organs: [],
      organProps: {
        chilidren: "children",
        label: "name"
      },
      organVisible: false,
      // 
      addFormVisible: false, // 
      // 
      officeForm: {
        id: "",
        parentId: 0,
        parentName: "",
        name: "",
        master: "",
        email: "",
        sort: "",
        useable: 0
      },
      officeFormRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        master: [{ required: true, message: "", trigger: "blur" }],
        email: [{ required: true, message: "", trigger: "blur" }]
        // sort: [
        //     {required: true, message: '', trigger: 'blur'}
        // ] todo: 
      },

      // 
      editFormVisible: false // 
    };
  },
  methods: {
    callback() {
      this.loadData();
    },
    // 
    loadData: function() {
      this.$service.get_offices_json().then(data => {
        this.tableData = data.officeTreeTable;
      });
    },
    // // 
    // handleRefresh: function () {
    //     window.location.href = localStorage.getItem("adminPath") + "/manage/office/index";
    // },

    // 
    handleChangetStatus: function(index, row) {
      var id = row.id;
      var useable = row.useable == 1 ? 0 : 1;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$service
            .changeOrganStatus({ id: id, useable: useable },"")
            .then(() => {
              this.callback();
            });
        })
        .catch(() => {
        });
    },

    // 
    // handleDetail: function(index, row) {
    // },
    selectOffice() {
      this.organVisible = true;
      this.$service.get_office_tree_json().then(data => {
        this.organs = data.officeTree;
      });
    },
    handleNodeClick(data) {
      this.$appState.$set("company_data", JSON.stringify(data));
    },
    companySelectCancel() {
      this.$appState.$set("company_data", "");
      this.organVisible = false;
    },
    companySelectOk() {
      this.organVisible = false;
      const data = JSON.parse(this.$appState.$get("company_data"));
      this.officeForm.parentId = data.id;
      this.officeForm.parentName = data.name;
    },
    // 
    handleAdd: function() {
      this.addFormVisible = true;
      this.officeForm.id = "";
      this.officeForm.parentId = "";
      this.officeForm.parentName = "";
      this.officeForm.name = "";
      this.officeForm.master = "";
      this.officeForm.email = "";
      this.officeForm.sort = "";
      this.officeForm.useable = 0;
    },
    // 
    addSubmit: function() {
      var _this = this;
      this.$refs.officeForm.validate(valid => {
        if (valid) {
          this.$service.addOrgan(_this.officeForm,"").then(() => {
            this.callback();
            this.addFormVisible = false;
          });
        } else {
          return false;
        }
      });
    },
    // 
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      this.officeForm.id = row.id;
      this.officeForm.parentId = row.parentId;
      if (row.parentName == null) {
        this.officeForm.parentName = "";
      } else {
        this.officeForm.parentName = row.parentName;
      }
      this.officeForm.name = row.name;
      this.officeForm.master = row.master;
      this.officeForm.email = row.email;
      this.officeForm.sort = row.sort;
      this.officeForm.useable = row.useable;
    },
    // editSubmit
    editSubmit: function() {
      var _this = this;
      this.$refs.officeForm.validate(valid => {
        if (valid) {
          this.$service.updateOrgan(_this.officeForm,"").then(() => {
            this.callback();
            this.editFormVisible = false;
          });
        } else {
          return false;
        }
      });
    },
    // 
    cancelAdd: function() {
      this.addFormVisible = false;
      this.$message({
        showClose: true,
        message: ""
      });
    },
    cancelEdit: function() {
      this.editFormVisible = false;
      this.$message({
        showClose: true,
        message: ""
      });
    },
    // 
    handleDelete: function(index, row) {
      var id = row.id;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "error"
      })
        .then(() => {
          this.$service.delOrgan({ id: id },"").then(() => {
            this.callback();
          });
        })
        .catch(() => {
          this.$message({
            showClose: true,
            message: ""
          });
        });
    }
  },
  created() {
    this.loadData();
  }
  // });
};
</script>
<style scoped lang="stylus">
.el-checkbox-group label
  width: 160px
.el-checkbox+.el-checkbox
  margin-left: 0px
.organSelect >>>.is-current
  color: #3980e2
</style>
