<template>
  <div>
    <!-- authority -->
    <div class="TopNav">
      <div class="left">
        <el-button-group>
          <el-button type="primary" size="small" @click="handleAdd" v-permission="'sysSetup:email:add'">
            <a class="fa fa-plus" style="color: white;"></a>
          </el-button>
          <el-button type="primary" size="small" @click="handleBatchDel" v-permission="'sysSetup:email:batchDel'">
            <a class="fa fa-trash" style="color: white;"></a> 
          </el-button>
        </el-button-group>
      </div>
      <div class="right">
        <el-form :inline="true" :model="searchForm" ref="searchForm" >
          <!-- TODO: permission -->
          <el-form-item label prop="emailName">
            <el-input
              v-model="searchForm.emailName"
              style="width: 200px"
              size="small"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label prop="type">
            <el-select v-model="searchForm.type" clearable filterable placeholder="">
              <el-option
                v-for="item in emailTypeList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" icon="search" @click="submitForm"></el-button>
            <el-button type="primary" size="small" @click="handleReset">
              <a class="fa fa-refresh" style="color: white;"></a> 
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- talbe -->
    <el-table
      ref="myTable"
      :data="tableData"
      style="width: 100%;"
      stripe
      border
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="id" label="ID" width="50"></el-table-column>
      <el-table-column prop="emailName" label="" width="230">
        <template slot-scope="scope">
          <el-icon name="message"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.emailName }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="comment" label="" width="200"></el-table-column>
      <el-table-column prop="createDate" label="" width="200">
        <template slot-scope="scope">
          <el-icon name="time"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.createDate }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="" width="100">
        <template slot-scope="scope">
          <el-tag type="success" close-transition="false" v-if="scope.row.status === 1">
            <a class="fa fa-unlock"></a> 
          </el-tag>
          <el-tag type="danger" close-transition="false" v-if="scope.row.status === 0">
            <a class="fa fa-lock"></a> 
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right">
        <template slot-scope="scope">
          <el-button-group>
            <!--<el-button size="small" type="success" @click="handleDetail(scope.$index, scope.row)">
                            <a class="fa fa-search" style="color: white;"></a> 
            </el-button>-->
            <el-button
              size="small"
              type="success"
              @click="handleChangetStatus(scope.$index, scope.row)"
              v-permission="'sysSetup:email:changeStatus'"
            >
              <a class="fa fa-edit" style="color: white;"></a> 
            </el-button>
            <el-button
              size="small"
              type="primary"
              @click="handleEdit(scope.$index, scope.row)"
              v-permission="'sysSetup:email:edit'"
            >
              <a class="fa fa-edit" style="color: white;"></a> 
            </el-button>
            <el-button
              size="small"
              type="info"
              @click="handleDelete(scope.$index, scope.row)"
              v-permission="'sysSetup:email:del'"
            >
              <a class="fa fa-trash" style="color: white;"></a> 
            </el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <!-- pagination -->
    <div align="right">
      <pagination
        v-bind:currentpage="currentPage"
        v-bind:pagesize="pageSize"
        v-bind:totalcount="totalCount"
        @handle-size-change="handleSizeChange"
        @handle-current-change="handleCurrentChange"
      ></pagination>
    </div>
    <!---->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="addFormVisible"
      v-if="addFormVisible"
      v-model="addFormVisible"
      :close-on-click-modal="false"
    >
      <el-form :model="emailForm" :rules="emailFormRules" ref="emailForm" label-width="100px">
        <el-form-item label="" prop="emailName">
          <el-input
            size="small"
            v-model="emailForm.emailName"
            placeholder=", :xxx@coocaa.com/xxx@skyworth.com"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="typeFlags">
          <el-checkbox-group v-model="emailForm.typeFlags">
            <el-checkbox
              v-for="item in emailTypeList"
              :value="item.value"
              :label="item.value"
              name="typeNames"
              :key="item.value"
            >{{item.label}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input size="small" type="textarea" v-model="emailForm.remarks" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="emailForm.status">
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelAdd"></el-button>
        <el-button type="primary" @click="addSubmit"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'emailNoticeAA',
  data() {
    var validatePass = (rule, value, callback) => {
      var c = value.indexOf("@coocaa.com");
      var s = value.indexOf("@skyworth.com");
      if (c <= 0 && s <= 0) {
        callback(new Error("coocaa.comskyworth.com"));
      } else {
        callback();
      }
    };
    return {
      // 
      tableData: [],
      dialogTitle: "",
      // 
      multipleSelection: [],
      //
      criteria: {},
      // 
      searchForm: {
        emailName: "",
        type: ""
      },
      emailTypeList: null,
      //  typeList: null,
      // 
      addFormVisible: false, // 
      // 
      emailForm: {
        id: "",
        emailName: "",
        // type: '',
        remarks: "",
        status: 0,
        typeFlags: []
        // 
      },
      emailFormRules: {
        emailName: [
          { required: true, message: "", trigger: "blur" },
          { type: "email", message: "" },
          { validator: validatePass, trigger: "change" }
        ],
        // type: [
        //     {type: 'number', required: true, message: '', trigger: 'change'}
        // ],
        typeFlags: [
          {
            type: "array",
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      // 
      editFormVisible: false, // 
      // :pageSize
      pageSize: 10,
      // :pageNum
      currentPage: 1,
      // 
      start: 1,
      // 
      totalCount: 1
    };
  },
  methods: {
    // 
    loadData: function() {
      this.criteria["pageNum"] = this.currentPage;
      this.criteria["pageSize"] = this.pageSize;
      this.$service.get_emails_json(this.criteria).then(data => {
        this.tableData = data.pageInfo.list;
        this.totalCount = data.pageInfo.total;
        this.emailTypeList = data.emailTypeList;
      });
    },
    // , 1020,val=20
    handleSizeChange: function(val) {
      this.pageSize = val;
      this.loadData();
    },
    // , 12,val=2
    handleCurrentChange: function(val) {
      this.currentPage = val;
      this.loadData();
    },
    // ,
    submitForm: function() {
      var _this = this;
      this.$refs.searchForm.validate(function(result) {
        if (result) {
          _this.criteria = _this.searchForm;
          _this.loadData();
        } else {
          return false;
        }
      });
    },
    // 
    handleReset: function() {
      this.$refs.searchForm.resetFields();
    },
    // 
    handleChangetStatus: function(index, row) {
      var id = row.id;
      var status = row.status == 1 ? 0 : 1;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$service
            .changeEmailStatus({ id: id, status: status },"")
            .then(() => {
              this.loadData();
            });
        })
        .catch(() => {
          this.$message({
            showClose: true,
            message: ""
          });
        });
    },

    // 
    // handleDetail: function(index, row) {
    //   var id = row.id;
    //   // todo: 
    // },
    // 
    handleAdd: function() {
      this.dialogTitle = "";
      this.addFormVisible = true;
      this.emailForm.id = "";
      this.emailForm.emailName = "";
      // this.emailForm.type = '';
      this.emailForm.typeFlags = [];
      this.emailForm.remarks = "";
      this.emailForm.status = 0;
    },

    // 
    addSubmit: function() {
      var _this = this;
      this.$refs.emailForm.validate(valid => {
        if (valid) {
          let emailForm = JSON.stringify(_this.emailForm);
          emailForm = JSON.parse(emailForm);
          emailForm.typeFlags = emailForm.typeFlags.join(",");
          if(this.emailForm.id!=""){
          this.$service.updateEmail(emailForm,"").then(() => {
            this.loadData();
            this.addFormVisible = false;
          });
          }else{
          this.$service.addEmail(emailForm,"").then(() => {
            this.loadData();
            this.addFormVisible = false;
          });
          }
        } else {
          return false;
        }
      });
    },

    // 
    handleEdit: function(index, row) {
      this.dialogTitle = "";
      this.addFormVisible = true;
      this.emailForm.id = row.id;
      this.emailForm.emailName = row.emailName;
      // this.emailForm.type = row.type;
      this.emailForm.typeFlags = row.typeIds;
      this.emailForm.remarks = row.remarks;
      this.emailForm.status = row.status;
    },
    // 
    cancelAdd: function() {
      this.addFormVisible = false;
      // this.$message({
      //     showClose: true,
      //     message: ''
      // });
    },
    cancelEdit: function() {
      this.editFormVisible = false;
      this.$message({
        showClose: true,
        message: ""
      });
    },

    // 
    handleDelete: function(index, row) {
      var id = row.id;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "error"
      })
        .then(() => {
          this.$service.delEmail({ id: id },"").then(() => {
            this.loadData();
          });
        })
        .catch(() => {
        });
    },

    // 
    handleSelectionChange: function(val) {
      // ,id(push)multipleSelection
      var ids = [];
      for (var i = 0; i < val.length; i++) {
        ids.push(val[i].id);
      }
      this.multipleSelection = ids;
    },

    // 
    handleBatchDel: function() {
      var ids = this.multipleSelection;
      if (ids.length > 0) {
        this.$confirm("?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "error"
        })
          .then(() => {
            this.$service.batchDelEmail({ ids: ids.join(",") },"").then(() => {
              this.loadData();
            });
          })
          .catch(() => {
          });
      } else {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
      }
    }
  },
  created() {
    this.loadData();
  }
};
</script>
<style scoped>
.el-checkbox-group label {
  width: 160px;
}
.el-checkbox + .el-checkbox {
  margin-left: 0px;
}
</style>

