<template>
  <div class="launch-list">
    <!-- authority -->
    <div class="TopNav">
      <div class="left">
        <el-button-group>
          <el-button
            type="primary"
            size="small"
            @click="handleAdd"
            v-permission="'hoder:launch:crowd:modify'"
          >
            <a class="fa fa-plus" style="color: white;"></a>
          </el-button>
        </el-button-group>
      </div>
      <div class="right">
        <!-- form search -->
        <el-form
          :inline="true"
          :model="searchForm"
          ref="searchForm"
          @submit.native.prevent="submitForm"
        >
          <el-form-item label prop="launchName">
            <el-input v-model="searchForm.launchName" style="width: 200px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" icon="search" @click="submitForm"></el-button>
            <el-button type="primary" size="small" @click="handleReset">
              <a class="fa fa-refresh" style="color: white;"></a> 
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- talbe -->
    <el-table ref="myTable" :data="tableData" style="width: 100%;" stripe border>
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="launchCrowdId" label="ID" width="50"></el-table-column>
      <el-table-column prop="launchName" label="" width="100"></el-table-column>
      <el-table-column prop="dmpCrowdId" label="Id" width="80"></el-table-column>
      <el-table-column prop="biName" label="" width="120"></el-table-column>
      <el-table-column prop="status" label="" width="70">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{launchStatusEnum[scope.row.status]}}</span>
        </template>
      </el-table-column>
      <!--<el-table-column prop="menuIds" label="" width="200"></el-table-column>-->
      <el-table-column prop="expiryTime" label="" width="180">
        <template slot-scope="scope">
          <el-icon name="time"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.expiryTime }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="dmpEstimate" label="" width="80"></el-table-column>
      <el-table-column prop="total1" label="" width="90"></el-table-column>
      <el-table-column prop="total3" label="openId" width="110"></el-table-column>
      <el-table-column prop="total2" label="openId" width="110"></el-table-column>
      <el-table-column prop="creatorName" label=""></el-table-column>
      <el-table-column prop="department" label=""></el-table-column>
      <el-table-column label="" fixed="right" min-width="200">
        <template slot-scope="scope">
          <el-button-group  class="button-group-position">
            <el-button
              v-if="scope.row.status==1"
              v-permission="'hoder:launch:crowd:launch'"
              size="small"
              type="warning"
              @click="lanuch(scope.$index, scope.row)"
            ></el-button>
            <el-button
              v-if="scope.row.status==3"
              v-permission="'hoder:launch:crowd:cancel'"
              size="small"
              type="warning"
              @click="cancelLanuch(scope.row)"
            ></el-button>
            <el-button
              size="small"
              type="success"
              v-permission="'hoder:launch:crowd:detail'"
              @click="condition(scope.row) "
            ></el-button>
            <el-button
              v-permission="'hoder:launch:crowd:modify'"
              size="small"
              type="primary"
              @click="handleEdit(scope.row.launchCrowdId)"
              shiro:hasPermission="sysAdministrative:role:edit"
            ></el-button>
            <el-button
              v-if="scope.row.status==1"
              v-permission="'hoder:launch:crowd:del'"
              size="small"
              type="info"
              @click="del(scope.row)"
            ></el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <!-- pagination -->
    <!-- pagination -->
    <div align="right">
      <pagination
        v-bind:currentpage="currentPage"
        v-bind:pagesize="pageSize"
        v-bind:totalcount="totalCount"
        @handle-size-change="handleSizeChange"
        @handle-current-change="handleCurrentChange"
      ></pagination>
    </div>
    <el-dialog title="" :visible.sync="isShowCondition" width="30%">
      <el-form>
        <el-form-item :label="item.policyName" v-for="item in selectStrategy" :key="item.policyName">
          <el-checkbox
            v-model="v.choosed"
            v-for="v in item.childs"
            :key="v.crowdId"
            disabled
          >{{v.crowdName}}
          </el-checkbox>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog :visible.sync="showEstimate">
      <div class="choose-tip"></div>
      <el-checkbox-group v-model="estimateValue" aria-required="true">
        <el-checkbox v-for="(item,index) in estimateItems" :value="index" :label="index" :key="index">{{item}}</el-checkbox>
      </el-checkbox-group>
      <div v-show='showError' class="error-msg"></div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="showEstimate = false"> </el-button>
    <el-button type="primary" @click="handleEstimate"></el-button>
  </span>
    </el-dialog>
  </div>
</template>
<script>
// import _ from "lodash";
export default {
  data() {
    return {
      // 
      tableData: [],
      launchStatusEnum: { "1": "", "2": "", "3": "" },
      //
      criteria: {},
      // 
      searchForm: {
        launchName: ""
      },
      // 
      // editFormVisible: false,// 
      // :pageSize
      pageSize: 10,
      // :pageNum
      currentPage: 1,
      // 
      start: 1,
      // 
      totalCount: 1,
      isShowCondition: false,
      selectStrategy: null,//
        showEstimate: false,
        estimateValue: ['0'],
        estimateItems: [],
        currentLaunchId: '',
        showError: false
    };
  },
  created() {
    this.loadData();
  },
  methods: {
    callback() {
        this.loadData();
    },
    handleAdd() {
      this.$emit("changeStatus", false);
    },
    handleEdit(launchCrowdId) {
      this.$emit("changeStatus", false, launchCrowdId);
    },
    condition(row) {
      this.isShowCondition = true;
      this.$service
        .ruleDetail({ launchCrowdId: row.launchCrowdId })
        .then(({ respcl }) => {
         // if (respcl.length > 0) {
            this.selectStrategy = respcl
          //}
        });
    },
    del(row) {
      var id = row.launchCrowdId;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$service.launchCrowdDel({ launchCrowdId: id },"").then(() => {
            this.callback();
          });
        })
        .catch(() => {
        });
    },
    // 
    loadData: function() {
      this.criteria["pageNum"] = this.currentPage;
      this.criteria["pageSize"] = this.pageSize;
      this.$service.crowdList(this.criteria).then(data => {
        this.tableData = data.list;
        this.totalCount = data.total;
      });
    },
    // , 1020,val=20
    handleSizeChange: function(val) {
      this.pageSize = val;
      this.loadData();
    },
    // , 12,val=2
    handleCurrentChange: function(val) {
      this.currentPage = val;
      this.loadData();
    },
    // ,
    submitForm: function() {
      // var _this = this;
      // this.$refs.searchForm.validate(function(result) {
      //   if (result) {
      //     _this.criteria = _this.searchForm;
      //     _this.loadData();
      //   } else {
      //     return false;
      //   }
      // });
        this.$refs.searchForm.validate((result) => {
            if (result) {
                this.criteria = this.searchForm
                this.loadData()
            } else {
                return false
            }
        })
    },
    // 
    handleReset: function() {
        this.searchForm.launchName = ''
        this.criteria = {}
        this.loadData()
    },
    // 
    lanuch: function(index, row) {
      this.currentLaunchId = row.launchCrowdId;
        this.showEstimate = true
        this.$service.getEstimateType().then((data) => {
            this.estimateItems = data
        })
      // this.$confirm("?", "", {
      //   confirmButtonText: "",
      //   cancelButtonText: "",
      //   type: "warning"
      // })
      //   .then(() => {
      //     this.$service.launchCrowd({ launchCrowdId: id },"").then(data => {
      //       this.callback();
      //     });
      //   })
      //   .catch(() => {
      //
      //   });
    },
    handleEstimate () {
        if (this.estimateValue.length === 0) {
            this.showError = true
            return
        } else {this.showError = false}
        let calIdType = this.estimateValue.map((item) => item).join(',')
        this.$service.launchCrowd({ launchCrowdId: this.currentLaunchId,calIdType: calIdType },"").then(() => {
            this.showEstimate = false
            this.callback();
        });
    },
    cancelLanuch(row) {
      var id = row.launchCrowdId;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$service.cancelLaunchCrowd({ launchCrowdId: id },"").then(() => {
            this.callback();
          });
        })
        .catch(() => {

        });
    },
    // 
    // handleDetail: function(index, row) {
    //   var id = row.id;
    //   // todo: 
    // }
  }
};
</script>
<style lang="stylus" scoped>
  .choose-tip
    margin 20px 0
    color red
  .launch-list >>> .button-group-position .el-button
    margin 3px
  .error-msg
    color red
    font-size 14px
    margin-top 10px
</style>
