export function estimateTemp(object) {
    const newFormData = new FormData()
    newFormData.append('crowdRule',JSON.stringify(object))
    return this.fetch({
        method: 'post',
        url: 'api/syncCrowdEstimate',
        data: newFormData
    })
}
